.adding-job-container{
   margin-right: 25%;
   padding: 30px;
    
    font-family: Rubik-regular;
    z-index: 100;
    display: flex;
    flex-direction: column;
    direction: rtl;
    width: 65%;
    border: 1px solid;
    justify-content: flex-start;
    background-color: white;
 
    
    label{
        // width: 100%;
        flex-wrap: nowrap;
        white-space: pre;
    }
input{
    padding: 7px;
    border-radius: 5px;
    border: 1px solid #b8b8b8;
    
    margin: 15px 5px;
}
.data-and-time{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-content: flex-start;
    align-self: flex-start;
    width: 100%;
  
}
.data-and-time-input{
    display: flex;
    justify-content: space-between;

}

.save-btn{
    width: 200px;
    height: 46px;
    background-color: #34018E;
    font-size: 18px;
    color: white;
    border-radius: 2px 15px;
    border: none;
    font-family: Rubik, sans-serif;
    align-content: center;
    align-items: center;
    align-self: center;
    justify-content: center;
}
.form-check{
    display: flex;
    flex-direction: row;
    
    flex-wrap: nowrap;
    white-space: pre;
    align-items: center;

 
}

}

.Description{
    width: 100%;
    display: flex;
    flex-direction: column;

}
textarea{
    padding: 7px;
    border-radius: 5px;
    border:1px solid #b8b8b8 ;
   
    margin: 15px 0;
}

.event-title{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.link-To-Linkedin{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.boxStyle{
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30%;
    height: 70%;
    background-color: white;
    text-align: right;
    color: #4A4A4A;
    box-shadow: 24px;
    padding: 4px;
    justify-content: center;
    align-items: center;

}

.add-file {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: right;
    border-radius: 5px;

    width: 100%;
    height: 100px;
    border: 1px solid #b8b8b8;


}

.chooseFromPC {
    padding: 8px;
    // width: 100%;
    background-color: #e7e7e7;
    text-align: center;
    color: #4A4A4A;
}

.selectBox select{
    padding: 7px;
    border-radius: 5px;
    border: 1px solid #b8b8b8;
}